.table-container {
  text-align: center;
  color: $white1 !important;

  .table-header,
  .table-line {
    display: grid;
    grid-template-columns: 308px 100px 100px 120px repeat(7, 1fr);
  }
  .table-header {
    height: 44px;
    align-items: center;

    span {
      input {
        width: 30px;
        font-family: $font1;
        background: none;
        border: none;
        outline: none;
        color: white;
        font-size: 1.05rem;
        color: $white1;
      }
    }

    .range-container {
      text-align: left;
      margin-left: 10px;
      display: grid;
      grid-template-columns: 60px 1fr;
      align-items: center;

      input[type="range"] {
        appearance: none;
        width: 180px;
        background: linear-gradient(90deg, #0debc6, $color1);
        box-shadow: 0 10px 20px rgba(0, 0, 0, 0.3);
        border-radius: 50px;
        height: 6px;
        transform: translate(6px, 1px);
        display: flex;
        align-items: center;
        cursor: pointer;
      }
      input[type="range"]::-webkit-slider-runnable-track {
        appearance: none;
        height: 10px;
      }
      input[type="range"]::-webkit-slider-thumb {
        appearance: none;
        width: 6px;
        height: 14px;
        transform: translateY(-2px);
        background: $white1;
        border-radius: 150px;
        cursor: grab;
        transition: 0.15s;
      }
      input[type="range"]::-webkit-slider-thumb:active {
        cursor: grabbing;
        transform: scale(2);
      }
    }
    input[type="radio"] {
      display: none;

      &:checked ~ label {
        font-weight: 900;
        color: $color1;
      }
    }
    label {
      user-select: none;
      cursor: pointer;
      transition: 0.1s;

      &:hover {
        color: $color1;
      }
    }
  }

  .table-line {
    height: 40px;
    align-items: center;
    border-top: 1px solid rgb(73, 73, 73);
    position: relative;
    color: $white1;
    transition: 0.15s;

    &:hover {
      background: rgba(128, 128, 128, 0.25);
    }
    .infos-container {
      display: grid;
      grid-template-columns: 32px 32px 1fr;
      height: 100%;
      align-items: center;

      .img {
        display: flex;
        align-items: center;
        justify-content: center;

        img {
          object-fit: cover;
        }
      }
      .infos {
        height: 100%;
        width: 100%;
        align-items: center;
        display: flex;
        text-align: left;

        .chart-img {
          transform: translateY(2px);
          z-index: 1000;
        }
        img {
          height: 14px;
          padding: 0 1px;
          transition: 0.1s;
          cursor: pointer;

          &:hover {
            transform: scale(1.3);
          }
        }
        a {
          transform: translateY(2px);
        }
        h4 {
          margin-left: 6px;
          letter-spacing: 1px;
        }
        span {
          color: $white2;
          margin: 0 2px 0 5px;
          font-size: 0.9rem;
        }
      }
      p {
        margin: 0 10px;
      }
    }
    .volume,
    .mktcap {
      color: $white2;
    }
  }
}
